    url("../fonts/YesevaOne-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Josefin Sans";
  src: url("../fonts/JosefinSans-Light.eot");
  src: local("Josefin Sans Light"), local("JosefinSans-Light"),
    url("../fonts/JosefinSans-Light.eot?#iefix") format("embedded-opentype"),
    url("../fonts/JosefinSans-Light.woff2") format("woff2"),
    url("../fonts/JosefinSans-Light.woff") format("woff"),
    url("../fonts/JosefinSans-Light.ttf") format("truetype");
